import React, { useEffect, useState } from 'react';
import axios from 'axios';





function Historial() {
  const [datos, setDatos] = useState([]);
  const llamardatos = async () => {

    try {
      // Realizar una solicitud POST a la URL deseada
      const response = await axios.get('https://fast-sea-94873.herokuapp.com/api/historial');

      // Manejar la respuesta del servidor
      setDatos(response.data);
      console.log("prueba",datos)


      // Puedes hacer algo con la respuesta del servidor aqu, por ejemplo, actualizar el estado de tu componente
      // o realizar alguna otra accin en tu aplicacin

    } catch (error) {
      // Manejar errores de la solicitud
      console.error('Error al crear datos:', error);
    }
  };
  useEffect(() => {
    llamardatos();
    console.log(datos)

  }, []);


  return (

    <table className="table">
        <thead>
          <tr>
            <th>ID</th>
            <th>Nombre</th>
            <th>Temperatura (C)</th>
            <th>Humedad (%)</th>
            <th>Fecha de creacin</th>
          </tr>
        </thead>
        <tbody>
          {datos.map(ubicacion => (
            <tr key={ubicacion.id}>
              <td>{ubicacion.id}</td>
              <td>{ubicacion.nombre}</td>
              <td>{ubicacion.temperatura}</td>
              <td>{ubicacion.humedad}</td>
              <td>{ubicacion.created_at}</td>
            
            </tr>
          ))}
        </tbody>
      </table>
    
 
  );
}

export default Historial;